import { useState } from "react";
import JobCard from "../component/JobCard";
import jobs from "../data/jobs";

export default function JobsPage() {
    const [searchTerm, setSearchTerm] = useState("");
    const [locationFilter, setLocationFilter] = useState("");
    const [categoryFilter, setCategoryFilter] = useState("");

    // Get unique categories for dropdown (assuming jobs have a 'category' property)
    const categories = [...new Set(jobs.map(job => job.category))];

    // Filter jobs based on search criteria
    const filteredJobs = jobs.filter(job => {
        const matchesSearch = job.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
                             job.description.toLowerCase().includes(searchTerm.toLowerCase());
        const matchesLocation = locationFilter === "" || 
                               job.location.toLowerCase().includes(locationFilter.toLowerCase());
        const matchesCategory = categoryFilter === "" || job.category === categoryFilter;
        
        return matchesSearch && matchesLocation && matchesCategory;
    });

    return (
        <div className="jobs-page">
            <div className="search-filters">
                {/* General search input */}
                <input
                    type="text"
                    placeholder="Search jobs..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="search-input"
                />
                
                {/* Location filter */}
                <input
                    type="text"
                    placeholder="Filter by location..."
                    value={locationFilter}
                    onChange={(e) => setLocationFilter(e.target.value)}
                    className="location-input"
                />
                
                {/* Category dropdown */}
                <select
                    value={categoryFilter}
                    onChange={(e) => setCategoryFilter(e.target.value)}
                    className="category-select"
                >
                    <option value="">All Categories</option>
                    {categories.map(category => (
                        <option key={category} value={category}>
                            {category}
                        </option>
                    ))}
                </select>
            </div>
            
            {/* Display search results count */}
            <div className="results-count">
                Found {filteredJobs.length} {filteredJobs.length === 1 ? "job" : "jobs"}
            </div>
            
            {/* Display filtered job cards */}
            <div className="job-listings">
                {filteredJobs.length > 0 ? (
                    filteredJobs.map(job => (
                        <JobCard key={job.id} job={job} />
                    ))
                ) : (
                    <p className="no-results">No jobs match your search criteria.</p>
                )}
            </div>
        </div>
    );
}